<template>
    <div class="container">
        <!--  -->
        <van-nav-bar fixed 
        left-arrow
        @click-left="$router.back()"
        title=""
        ></van-nav-bar>

        <!--  -->
        <div class="chat-list">
            <div v-for="(obj,index) in list" :key="index">
                <!--  -->
                <div class="chat-item left" v-if="obj.name === 'xs'">
                    <van-image fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg"/>
                    <div class="chat-pao">{{obj.msg}}</div>
                </div>

                <!--  -->
                <div class="chat-item right" v-else>
                    <div class="chat-pao">{{obj.msg}}</div>
                    <van-image fit="cover" round :src="$store.state.userPhoto"/>
                </div>
            </div>
        </div>

        <!--  -->
        <div class="reply-container van-hairline--top">
            <van-field v-model="word" placeholder="...">
                <template #button>
                    <span style="font-size:12px;color:#999" @click="sendFn"></span>
                </template>
            </van-field>
        </div>
    </div>
</template>

<script>
import { io } from 'socket.io-client'
import {getToken} from '@/utils/token.js'
export default {
    name: 'Chat',
    data () {
        return {
            word: '', // 
            // 
            list: [
                //  name 
                { name: 'xs', msg: 'hi,!' },
                { name: 'me', msg: '' }
            ],
            socket: null, // socket
        }
    },
    created() {
        // 
        this.socket = io('http://toutiao.itheima.net', {
            query: {
                token: getToken()   // token
            },
            transports:['websocket']
        })

        // 
        this.socket.on('connect', () => {
            console.log('');
        })

        // 
        this.socket.on('message', obj => {
            this.list.push({
                name: 'xs',
                msg:obj.msg
            })

            // 
            this.$nextTick(() => {
                const theDiv = document.querySelector('.chat-list>div:last-child')
                theDiv.scrollIntoView({
                    behavior:'smooth'
                })
            })
        })
    },
    methods: {
        // span -> 
        sendFn() {
            if(this.word.trim().length === 0) return
            // socket.emit('')
            this.socket.emit('message', {
                msg: this.word,
                timestamp:new Date().getTime()
            })

            // 
            this.list.push({
                msg: this.word,
                name:'me'
            })

            // 
            this.$nextTick(() => {
                const theDiv = document.querySelector('.chat-list>div:last-child')
                theDiv.scrollIntoView({
                    behavior: 'smooth'
                })
            })

            this.word = ''

        }
    },
    destroyed() {
        this.socket.close()
        this.socket = null
    }
}
</script>

<style scoped lang="less">
.container{
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    box-sizing: border-box;
    background: #fafafa;
    padding: 46px 0 50px 0;
    /deep/ .van-icon{
        color: #fff;
    }
    .chat-list{
        height: 100%;
        overflow-y: scroll;
        .chat-item{
            padding: 10px;
            .van-image{
                vertical-align: top;
                width: 40px;
                height: 40px;
            }
            .chat-pao{
                vertical-align: top;
                display: inline-block;
                min-width: 40px;
                max-width: 70%;
                min-height: 40px;
                line-height: 38px;
                border: 0.5px solid #c2d9ea;
                border-radius: 4px;
                position: relative;
                padding: 0 10px;
                background-color: #e0effd;
                word-break: break-all;
                font-size: 14px;
                color: #333;
                &::before{
                    content: '';
                    width: 10px;
                    height: 10px;
                    position: absolute;
                    top: 12px;
                    border-top: 0.5px solid #c2d9ea;
                    border-right: 0.5px solid #c2d9ea;
                    background: #e0effd;
                }
            }
        }
    }
}
.chat-item.right{
    text-align: right;
    .chat-pao{
        margin-left: 0;
        margin-right: 15px;
        &::before{
            right:-6px;
            transform: rotate(45deg);
        }
    }
}
.chat-item.left{
    text-align: left;
    .chat-pao{
        margin-left: 15px;
        margin-right: 0;
        &::before{
            left: -5px;
            transform: rotate(-135deg);
        }
    }
}
.reply-container{
    position: fixed;
    left: 0;
    bottom: 0;
    height: 44px;
    width: 100%;
    background: #f5f5f5;
    z-index: 9999;
}
</style>